import { Component, ViewChild } from '@angular/core';
import { NavController, NavParams, ViewController, Platform, ModalController, AlertController, ToastController } from 'ionic-angular';

import { ModalPage } from '../modal/modal'
import { ModalPageIngresarPage } from '../modal-page-ingresar/modal-page-ingresar'
import { Observable } from 'rxjs/Observable';
import { NotesService } from '../../services/notes.service';
import { AngularFireAuth } from 'angularfire2/auth';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  
  private notes: any = [
    
 ];
  url: any;
  usuariouid;
  
  


  constructor(public navCtrl: NavController,  public viewCtrl : ViewController, public modalCtrl: ModalController, private platform:Platform,  public noteService:NotesService, public alertCtrl:AlertController, 
    private afAuth: AngularFireAuth, private toast: ToastController, public navParams:NavParams ) {

      this.noteService.getNotes().valueChanges()
      .subscribe(notas=>{
            this.notes = notas;

        } );
      
      
 
} 

   ionViewWillLoad(){
   
    
    this.afAuth.authState.subscribe(data => {
      if (data.email && data.uid){
        this.usuariouid=data.uid;
    console.log(this.usuariouid);
          this.toast.create({
              message: "Inicio se sesión exitoso!",
              duration: 500,
              position: 'bottom'
              
          }).present();
      }
     

    }
    
       
    );


  }
  

  


        verpaciente(id) {

  
let modal = this.modalCtrl.create(ModalPage,{id:id} );



    
    modal.present();
    

  };
  

  nuevopac(){

    let modal = this.modalCtrl.create(ModalPage,{id:0} );
    modal.present();
  }

  acomodar(){
    this.notes.sort(function (a, b) {
      return a.cama - b.cama;
     
    });
   


  }
  
  

}



