import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';

import { AboutPage } from '../pages/about/about';
import { ContactPage } from '../pages/contact/contact';
import { HomePage } from '../pages/home/home';
import { TabsPage } from '../pages/tabs/tabs';
import { PreguntasPage } from '../pages/preguntas/preguntas';
import { ModalPage } from '../pages/modal/modal';
import { Modal4Page } from '../pages/modal4/modal4';
import { ModalPageIngresarPage } from '../pages/modal-page-ingresar/modal-page-ingresar';

import { AngularFireModule } from 'angularfire2';
import { AngularFireDatabaseModule, AngularFireDatabase } from 'angularfire2/database';
import { AngularFireAuthModule } from 'angularfire2/auth';
import { AngularFireStorageModule } from 'angularfire2/storage';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { InAppBrowser } from '@ionic-native/in-app-browser';


import { CallNumber } from '@ionic-native/call-number';
import { Camera } from '@ionic-native/camera';


import { NotesService } from '../services/notes.service';
import { PreguntasService } from '../services/preguntas.service';

export const firebaseConfig = {
  apiKey: "AIzaSyDWA4wmWdgaJt9sWRv_I5xy_3OM4l-G1Ow",
  authDomain: "nxcxjim.firebaseapp.com",
  databaseURL: "https://nxcxjim.firebaseio.com",
  storageBucket: "nxcxjim.appspot.com",
  messagingSenderId: "873366827067"
};


@NgModule({
  declarations: [
    MyApp,
    AboutPage,
    ContactPage,
    HomePage,
    PreguntasPage,
    TabsPage,
    ModalPage,
    Modal4Page,
    ModalPageIngresarPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    AngularFireModule.initializeApp(firebaseConfig),
    AngularFireDatabaseModule,
    AngularFireAuthModule,
    AngularFireStorageModule
    
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    AboutPage,
    ContactPage,
    HomePage,
    PreguntasPage,
    TabsPage,
    ModalPage,
    Modal4Page,
    ModalPageIngresarPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    ModalPageIngresarPage,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
       
    NotesService,
    PreguntasService,
    CallNumber,
    Camera,
    InAppBrowser

  ]
})
export class AppModule {}



