import { Component } from '@angular/core';
import {  NavController, NavParams, ToastController } from 'ionic-angular';
import { AngularFireAuth } from 'angularfire2/auth';
import {User} from "../../models/User";
import { TabsPage } from '../tabs/tabs';
/**
 * Generated class for the ModalPageIngresarPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@Component({
  selector: 'page-modal-page-ingresar',
  templateUrl: 'modal-page-ingresar.html',
})
export class ModalPageIngresarPage {


  user= {} as User;
  correo;

  constructor(public navCtrl: NavController, public navParams: NavParams, private afAuth: AngularFireAuth,
private toast: ToastController  ) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ModalPageIngresarPage');
  };
   async  login(user:User){

      this.correo= user.email
      

    try {  
      const result = await this.afAuth.auth.signInWithEmailAndPassword(user.email,user.password);
      if(result){
        this.navCtrl.setRoot(TabsPage,user.email);

      }
      
    }
    catch (e) {
		this.toast.create({
        message: "Algo falló al iniciar sesión, verifique sus datos e intente de nuevo.",
        duration: 3000,
        position: "middle"
    }).present();
      console.error(e);
    };
     
    
  }

  

}



