import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, ToastController } from 'ionic-angular';
import { PreguntasService } from '../../services/preguntas.service';
import { Observable } from 'rxjs/Observable';
import * as firebase from 'firebase';
import { PreguntasPage } from '../preguntas/preguntas';

/**
 * Generated class for the Modal4Page page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-modal4',
  templateUrl: 'modal4.html',
})
export class Modal4Page {

  @ViewChild('slides') 
  
  slides: any;
  slideOptions: any;

 pregs: any = [    
 ];

 score: number = 0;

  numeroalea:any;

  id : any = null;
  imagenpreg;

  numpregs:any;
  ultima= false;
  siguienteboton= true;

  contadorpreg: number=1 ;

  formattedDate;

  respondio: boolean = false;

  color;

  

  constructor(public navCtrl: NavController, public navParams: NavParams, public preguntasService: PreguntasService, public viewCtrl: ViewController,
    private toast: ToastController) {
  
    
    this.preguntasService.getPreguntas().valueChanges()
    .subscribe( preguntas =>{
          this.pregs = preguntas;
          this.pregs.map((preguntas) => {
            console.log(this.pregs)
            
            preguntas = this.randomizePreguntas(this.pregs);
            return preguntas;
           
          }) ;

          this.pregs.map((preguntas) => {
            console.log(this.pregs)
            let originalOrder = preguntas.opciones;
            preguntas.opciones = this.randomizeAnswers(originalOrder);
            return preguntas;
           
          } 
        );
          
      } );



      this.getFormattedDate();
     
      
      
   
  
  }

 ionViewDidLoad(){
  this.slides.lockSwipes(true);
  this.numpregs=this.navParams.get('numpregs');

 }

 getSomeText() {

  firebase.storage().ref('imagendepreg/'+this.pregs.id+'/').child('imagenpregunta.jpg').getDownloadURL()
    .then(response => {this.imagenpreg = response;
                       
                       
                      
    } )
    .catch(error => console.log('error', error));

  }


  dismiss()
  {

    this.viewCtrl.dismiss();
  }

  randomizePreguntas(rawPreguntas: any[]): any[]{
    for (let i = rawPreguntas.length - 1; i>0; i--){
      let j = Math.floor(Math.random() * (i+1));
      let temp = rawPreguntas[i];
      rawPreguntas[i] = rawPreguntas[j];
      rawPreguntas[j] = temp;

    }


    return rawPreguntas;



  }

  randomizeAnswers(rawAnswers: any[]): any[]{
    for (let i = rawAnswers.length - 1; i>0; i--){
      let j = Math.floor(Math.random() * (i+1));
      let temp = rawAnswers[i];
      rawAnswers[i] = rawAnswers[j];
      rawAnswers[j] = temp;

    }


    return rawAnswers;



  }

  siguientepreg(){
    this.slides.lockSwipes(false);
    this.slides.slideNext();
    this.slides.lockSwipes(true);
    this.respondio = false;

    this.contadorpreg ++ ;

    

  if( this.contadorpreg==this.numpregs ){
      this.ultima= true;
      this.siguienteboton= false;
    }
    return console.log(this.contadorpreg, this.ultima);

}

seleccionaRespuesta(opcion, pregunta){
 
  this.respondio = true;
  opcion.selected = true;
  
 

  if(opcion.correcta){
      this.score++;
      let toast = this.toast.create({
        message: 'Respuesta correcta!',
        duration: 500,
        position: 'middle'
      });
      toast.present();
      
      
  }
  else{
    let toast = this.toast.create({
      message: 'La respuesta correcta era: '+pregunta.respuesta,
      duration: 1500,
      position: 'middle'
    });
    toast.present();
  }
  
    
  
}

finalizar(){

  let resultadoexamen: any =
    {
      id:Date.now(),
      aciertos: this.score,
      fecha: this.formattedDate,
      numeropreguntas: this.numpregs

    };
  
 
  this.viewCtrl.dismiss();
  this.preguntasService.crearresultado(resultadoexamen);

}

getFormattedDate(){

  var dateObj = new Date()
  var year = dateObj.getFullYear().toString()
  var month = dateObj.getMonth().toString()
  var date = dateObj.getDate().toString()

  var monthArray = ["Ene", "Feb", "Mar", "Abr", "May", "Jun", "Jul", "Ago", "Sept", "Oct", "Nov", "Dic"]

  this.formattedDate = date + "/" + monthArray [month] + "/" + year;
 }



}



