import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, ToastController, AlertController } from 'ionic-angular';

import { Modal4Page } from '../modal4/modal4';
import { ModalPageIngresarPage } from '../modal-page-ingresar/modal-page-ingresar'

import { AngularFireAuth } from 'angularfire2/auth';

import { PreguntasService } from '../../services/preguntas.service';

/**
 * Generated class for the PreguntasPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-preguntas',
  templateUrl: 'preguntas.html',
})
export class PreguntasPage {

 
  numpregs:any;

  currentDate;
  formattedDate;

  hora: number=0;
  minutos: number=0;
  segundos: number=0;
  duracion:any;

  usuariouid;
  private results: any = [    
  ];

  ocultar:boolean = false;
  

  constructor(public navCtrl: NavController, public navParams: NavParams, public modalCtrl: ModalController,private afAuth: AngularFireAuth,
    public preguntasService: PreguntasService, private toast: ToastController, public alertCtrl:AlertController ) {

      

          
          
      } 
   
 
 
  

  ionViewDidLoad() {

    this.afAuth.authState.subscribe(data => {
      if (data.email && data.uid){
        this.usuariouid=data.uid;
    
          
      }
     

    }
    
       
    );
  
  this.preguntasService.getResultados().valueChanges()
  .subscribe( resultados =>{
        this.results = resultados;
        console.log(this.results)
        
          
         
        }) ;
        
    
    
    
  }

  borrarresultado(id){
    let alert = this.alertCtrl.create({
      title: 'Atención',
      subTitle: 'Se borrará la entrada de manera permanente',
      buttons: [
       
        {
          text: 'Aceptar',
          handler: () => {
            
            this.preguntasService.borrarresult(id);
            
            let toast = this.toast.create({
              message: 'Resultado borrado',
              duration: 1000,
              position: 'bottom'
            });
            toast.present();
            
          }
        },
        {
          text: 'Cancelar',
          handler: () => {
              
          }
        },


       ]
    });
    alert.present();

    
  }


  iniciarpreguntas(pregs) {
    console.log(this.numpregs);

   if(this.numpregs == undefined){

    
    this.toast.create({
      message: "Debes elegir al menos 1 pregunta",
      duration: 500,
      position: 'bottom'
  }).present();

    

   }

   else {
    let modal = this.modalCtrl.create(Modal4Page,{pregs:pregs, numpregs: this.numpregs} );
    
    
    
    
        
    modal.present();
   
   }
    
        
    
      };

mostrarResultados(){  
      this.ocultar= !this.ocultar;
     

      

    }
}



